@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button {
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  outline: none;

  font-family: "Noto Sans JP", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;

  box-shadow: 0px 2px 3px rgba(51, 51, 51, 0.2);
  cursor: pointer;
}

.button:hover,
.button:focus {
  background-color: #aeaeae;
}

.button--disable-shadow {
  box-shadow: none;
}

.button--color-default {
  background-color: #e0e0e0;
  color: #3f3f3f;
}

.button--color-primary {
  background-color: #3d5afe;
  color: white;
}

.button--color-primary:hover,
.button--color-primary:focus {
  background-color: #0039cb;
}

.button--color-secondary {
  background-color: #455a64;
  color: white;
}

.button--color-secondary:hover,
.button--color-secondary:focus {
  background-color: #1c313a;
}

.button--color-danger {
  background-color: #d32f2f;
  color: white;
}

.button--color-danger:hover,
.button--color-danger:focus {
  background-color: #9a0007;
}

.button--variant-outline {
  border: 1px solid #3d5afe;
  color: #3d5afe;
  background: none;
}

.button--variant-outline:hover,
.button--variant-outline:focus {
  background-color: rgba(41, 98, 255, 0.1);
}

.button--variant-text {
  background: none;
  color: #3d5afe;
}

.button--variant-text:hover,
.button--variant-text:focus {
  background-color: rgba(41, 98, 255, 0.1);
}

.button--disabled {
  color: #9e9e9e;
  background-color: #e0e0e0;
  cursor: not-allowed;
}
.button--disabled:hover,
.button--disabled:focus {
  background-color: #e0e0e0;
}

